import React, { Component } from 'react';
import '../../scss/_plan-list.scss';

class PlanList extends Component {
    state = {
        data: [],
        description: [],
        id: [],
        name: [],
        weekNumber: "",
        isEditInput: false,
        isSuccess: false,
        isRemoved: false,
        editInputValue: "",
        recipeId: 0,
        nameErr: "",
        weekNumberErr: "",
        monday: [],
        tuesday: [],
        wednesday: [],
        thursday: [],
        friday: [],
        saturday: [],
        sunday: [],
        meals: ["breakfast", "lunch", "soup", "main", "dinner"],
        weekdays: [{ pl: "poniedziaek", en: "monday" }, { pl: "wtorek", en: "tuesday" }, { pl: "roda", en: "wednesday" }, { pl: "czwartek", en: "thursday" }, { pl: "pitek", en: "friday" }, { pl: "sobota", en: "saturday" }, { pl: "niedziela", en: "sunday" }],
        recipes: [],
    }

    componentDidMount = () => {
        fetch("http://localhost:3000/schedules/")
            .then(response => response.json())
            .then(data => {
                data.forEach(el =>
                    this.setState({
                        description: el.description,
                        id: el.id,
                        name: el.name,
                        weekNumber: el.weekNumber,
                        monday: el.monday,
                        tuesday: el.tuesday,
                        wednesday: el.wednesday,
                        thursday: el.thursday,
                        friday: el.friday,
                        saturday: el.saturday,
                        sunday: el.sunday
                    }));
                this.setState({
                    data: data,
                });
                console.log(this.state.data);
            });

        fetch("http://localhost:3000/recipes/")
            .then(res => res.json())
            .then(recipes => {
                console.log(recipes)
                this.setState({
                    recipes
                })
            });
    };

    handleDeleteRecipe = (e) => {
        const tempArr = [...this.state.data];
        tempArr.forEach((el, id) => {
            if (+e.target.dataset.id === id) {
                tempArr.splice(id, 1)
                this.setState({
                    recipeId: el.id
                }, () => {
                    fetch(`http://localhost:3000/schedules/${this.state.recipeId}`, {
                        method: 'DELETE',
                    })
                        .then(resp => resp.json())
                        .then(data => console.log(data))
                })
            }
        });
        this.setState({
            data: tempArr,
            isRemoved: true
        });
    };

    handelEditRecipe = (e) => {
        const tempArr = [...this.state.data];
        tempArr.forEach((el, id) => {
            if (+e.target.dataset.id === id) {
                this.setState({
                    isEditInput: !this.state.isEditInput,
                    recipeId: el.id,
                    name: el.name,
                    description: el.description,
                    weekNumber: el.weekNumber,
                    monday: el.monday,
                    tuesday: el.tuesday,
                    wednesday: el.wednesday,
                    thursday: el.thursday,
                    friday: el.friday,
                    saturday: el.saturday,
                    sunday: el.sunday
                })
            }
        })
    };

    validate = () => {

        let nameErr = "";
        let weekNumberErr = "";

        const { name, weekNumber } = this.state

        if (!name.length) {
            nameErr = "Required field"
        }

        if (!weekNumber.length) {
            weekNumberErr = "Required field"
        }

        if (parseFloat(weekNumber) < 1 || parseFloat(weekNumber) > 52) {
            weekNumberErr = "Invalid field value"
        }

        if (nameErr || weekNumberErr) {
            this.setState({ nameErr, weekNumberErr })
            return false;
        }

        return true
    };

    closeAndSave = (e, name, description, weekNumber, monday, tuesday, wednesday, thursday, friday, saturday, sunday) => {

        e.preventDefault()
        const isValid = this.validate()
        const data = {
            name,
            description,
            weekNumber,
            monday,
            tuesday,
            wednesday,
            thursday,
            friday,
            saturday,
            sunday
        };

        if (isValid) {
            const tempArr = [...this.state.data];
            tempArr.forEach(el => {
                if (el.id === this.state.recipeId) {
                    el.name = this.state.name;
                    el.description = this.state.description;
                    el.weekNumber = this.state.weekNumber;
                    el.monday = this.state.monday;
                    el.tuesday = this.state.tuesday;
                    el.wednesday = this.state.wednesday;
                    el.thursday = this.state.thursday;
                    el.friday = this.state.friday;
                    el.saturday = this.state.saturday;
                    el.sunday = this.state.sunday
                }
            });
            this.setState({
                data: tempArr,
                name: "",
                nameErr: "",
                description: "",
                weekNumber: "",
                weekNumberErr: "",
                monday: ["", "", "", "", ""],
                tuesday: ["", "", "", "", ""],
                wednesday: ["", "", "", "", ""],
                thursday: ["", "", "", "", ""],
                friday: ["", "", "", "", ""],
                saturday: ["", "", "", "", ""],
                sunday: ["", "", "", "", ""],
                isSuccess: true,
                isEditInput: false,
            })


            fetch(`http://localhost:3000/schedules/${this.state.recipeId}`, {
                method: 'PATCH',
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify(data),
            })
                .then(response => response.json())
                .then(data => {
                    console.log('Success:', data);
                })
                .catch((error) => {
                    console.error('Error:', error);
                });

        }
    };

    handleValue = (weekday, index) => {

        if (weekday === "monday") {
            return this.state.monday[index]
        }

        if (weekday === "tuesday") {
            return this.state.tuesday[index]
        }

        if (weekday === "wednesday") {
            return this.state.wednesday[index]
        }

        if (weekday === "thursday") {
            return this.state.thursday[index]
        }

        if (weekday === "friday") {
            return this.state.friday[index]
        }

        if (weekday === "saturday") {
            return this.state.saturday[index]
        }

        if (weekday === "sunday") {
            return this.state.sunday[index]
        }
    };

    planMealSelected = (e, index, weekday) => {
        const val = e.target.value;

        if (e.target.id === weekday) {
            this.setState(prevState => {
                const newState = prevState[weekday].slice();
                newState[index] = val;
                return {
                    [weekday]: newState
                };
            });
        }
    };

    formChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    };

    render() {
        return (
            <div className="plan-list__container">
                <div className="plans-header">
                    <p>Plan List</p>
                    {/* <span class="fas fa-plus-square"></span> */}
                </div>
                {this.state.isEditInput && <div className="modal__popup-add-schedule" style={{ display: this.state.display }}>
                    <form className="modal__popup-add-schedule__form" onSubmit={(e) => this.closeAndSave(e, this.state.name, this.state.description, this.state.weekNumber, this.state.monday, this.state.tuesday, this.state.wednesday, this.state.thursday, this.state.friday, this.state.saturday, this.state.sunday)}>
                        <header className="modal__popup-add-schedule__form__header">
                            <h1 className="modal__popup-add-schedule__form__header__title">Edit plan</h1>
                            <button className="modal__popup-add-schedule__form__header__btn" type="submit">Save and close<i className="fa fa-window-close" aria-hidden="true"></i></button>
                        </header>
                        <span className="modal__popup-add-schedule__form__division"></span>
                        <p style={{ fontSize: "1rem", color: "red" }}>{this.state.nameErr}</p>
                        <div className="modal__popup-add-schedule__form__row">
                            <label className="modal__popup-add-schedule__form__label" htmlFor="name">Plan name</label>
                            <input maxLength="50" type="text" id="name" value={this.state.name} onChange={this.formChange}></input>
                        </div>
                        <div className="modal__popup-add-schedule__form__row">
                            <label className="modal__popup-add-schedule__form__label" htmlFor="description">Plan description</label>
                            <textarea maxLength="360" id="description" value={this.state.description} onChange={this.formChange}></textarea>
                        </div>
                        <p style={{ fontSize: "1rem", color: "red" }}>{this.state.weekNumberErr}</p>
                        <div className="modal__popup-add-schedule__form__row">
                            <label className="modal__popup-add-schedule__form__label" htmlFor="weekNumber">Week number</label>
                            <input type="number" id="weekNumber" value={this.state.weekNumber} onChange={this.formChange}></input>
                        </div>
                        <span className="modal__popup-add-schedule__form__division modal__popup-add-schedule__form__division-shorter"></span>
                        <table className="modal__popup-add-schedule__form__table">
                            <thead>
                                <tr>
                                    <th></th>
                                    {this.state.meals.map(meal => {
                                        return <th key={meal}>{meal}</th>
                                    })}
                                </tr>
                            </thead>
                            <tbody>
                                {this.state.weekdays.map(weekday => {
                                    return (
                                        <tr key={weekday.en}>
                                            <th>{weekday.en}</th>
                                            {this.state.meals.map((meal, index) => {
                                                return (
                                                    <td key={meal}>
                                                        <select value={this.handleValue(weekday.en, index)} onChange={(e) => this.planMealSelected(e, index, weekday.en)} id={weekday.en}>
                                                            <option>Choose</option>
                                                            {this.state.recipes.map(recipe => {
                                                                return <option key={recipe.id} value={recipe.name}>{recipe.name}</option>
                                                            })}
                                                        </select>
                                                    </td>
                                                )
                                            })}
                                        </tr>
                                    )
                                })}
                            </tbody>
                        </table>
                    </form>
                </div>}
                <div className="plans-info">
                    <p className="plans__info__id">ID</p>
                    <p className="plans__info__name">Name</p>
                    <p className="plans__info__description">Description</p>
                    <p className="plans__info__week">Week</p>
                    <p className="plans__info__action">Action</p>
                </div>
                <div className="plans__list">
                    <div className="plans plans__id">
                        {this.state.data.map(el => {
                            return <span className="plans__id">{el.id}</span>
                        })}
                    </div>
                    <div className="plans plans__name">
                        {this.state.data.map(el => {
                            return <span className="plans__name">{el.name}</span>
                        })}
                    </div>
                    <div className="plans plans__description">
                        {this.state.data.map(el => {
                            return <span className="plans__description">{el.description}</span>
                        })}
                    </div>
                    <div className="plans plans__week">
                        {this.state.data.map(el => {
                            return <span className="plans__week">{el.weekNumber}</span>
                        })}
                    </div>
                    <div className="plans__action">
                        {this.state.data.map((el, id) => {
                            return (
                                <div className="plans__action">
                                    <span
                                        data-id={id}
                                        class="far fa-edit"
                                        onClick={this.handelEditRecipe}>
                                    </span>
                                    <span
                                        data-id={id}
                                        class="far fa-trash-alt"
                                        onClick={this.handleDeleteRecipe}>
                                    </span>
                                </div>)
                        })}
                    </div>
                </div>
                {this.state.isSuccess && <div className="modal__edit-msg">
                    <button className="modal__edit-msg__btn" onClick={() => this.setState({ isSuccess: false })}><i className="fas fa-times"></i></button>
                    <i className="far fa-check-circle modal__edit-msg__icon" ></i>
                    <span className="modal__edit-msg__text">You've edited your plan!</span>
                </div>}
                {this.state.isRemoved && <div className="modal__delete-msg">
                    <button className="modal__delete-msg__btn" onClick={() => this.setState({ isRemoved: false })}><i className="fas fa-times"></i></button>
                    <i className="far fa-check-circle modal__delete-msg__icon"></i>
                    <span className="modal__delete-msg__text">You've deleted your plan!</span>
                </div>}
            </div>
        );
    }
}

export default PlanList;